.header {
	height: 100vh;
	width: 100%;
	&__wrap {
		display: flex;
		justify-content: flex-end;
		align-items: center;
		height: 100%;
		// margin-top: 40vh
		h1 {
			position: relative;
			color: $text-color;
			font-size: 40px;
			font-weight: 200;
			text-align: center;
			padding-bottom: 30px;
			&:after {
				content: '';
				position: absolute;
				width: 270px;
				height: 35px;
				top: 100%;
				left: 25%;
				background: url('/icons/decoration.svg') no-repeat center center; } } } }
.nav {
	position: fixed;
	right: 175px;
	top: 50px;
	z-index: 1;
	&__list {
		display: flex;
		align-items: center;
		justify-content: flex-end; }
	&__link {
		text-decoration: none;
		color: $text-color;
		font-size: 18px;
		font-weight: 300;
		padding: 15px 20px;
		border: 1px solid transparent;
		&:hover {
			border: 1px solid #686868; } } }
.features {
	min-height: 390px;
	margin: 0;
	padding: 80px;
	&__list {
		display: grid;
		grid-template-columns: repeat(3, 1fr);
		grid-gap: 180px; }
	&__item {
		text-align: center;
		span {
			display: block;
			text-transform: uppercase;
			font-size: 18px;
			color: $text-color;
			font-weight: bold;
			margin: 25px 0; }
		p {
			font-size: 16px;
			color: $text-color;
			font-weight: 300; } } }
.specials {
	padding: 80px 0;
	&__wrap {
		margin-top: 80px; }
	&__list {
		display: grid;
		grid-template-columns: repeat(6, 1fr); }
	&__item {
		display: flex;
		flex-direction: column;
		justify-content: center;
		align-items: center;
		text-align: center; }
	&__name {
		position: relative;
		font-size: 18px;
		line-height: 1.12;
		font-weight: bold;
		padding-bottom: 15px;
		margin-bottom: 20px;
		color: $text-color;
		border-bottom: 1px solid $text-color; }

	&__text {
		font-size: 16px;
		font-weight: 300;
		color: $text-color;
		margin-bottom: 20px;
		line-height: 1.66; } }
.about {
	background-color: #eeefef !important;
	height: 970px;
	&__wrap {
		height: 100%;
		display: grid;
		grid-template-columns: 1fr 1fr; }
	&__title {
		h2 {
			width: 100%; } }

	&__title {
		text-align: center;
		display: flex;
		flex-direction: column;
		justify-content: center;
		align-items: center; }
	&__text {
		display: block;
		margin-top: 50px;
		font-size: 30px;
		line-height: 1.8;
		font-weight: 300; } }
.menu {
	padding: 30px;
	min-height: 967px;
	.menu-nav {
		position: static;
		display: flex;
		justify-content: center;
		margin: 70px 0;
		right: 0;
		top: 0; }
	&__list {
		display: grid;
		grid-template-columns: 1fr 1fr;
		grid-gap: 20px 220px;
		span {
			display: block;
			color: $text-color; } }
	&__wrap {
		display: flex;
		justify-content: space-between; }
	&__name {
		text-transform: uppercase;
		font-size: 18px;
		font-weight: 300; }
	&__components {
		font-size: 16px;
		font-weight: 300;
		font-style: italic; } }
.footer {
	position: relative;
	padding: 60px 0;
	min-height: 950px;
	&__wrap {
		display: grid;
		grid-template-columns: 1fr 1fr; }
	&__content {
		padding: 0 95px; }
	&__contacts {
		margin-top: 50px;
		display: flex;
		justify-content: space-between; }
	.contacts {
		display: flex;
		flex-direction: column;
		margin-bottom: 80px;
		a {
			font-size: 16px;
			font-weight: 300;
			color: $text-color;
			text-decoration: none;
			line-height: 1.5; }
		&__phone {
			font-size: 16px;
			font-weight: 300;
			line-height: 1.5;
			color: $text-color;
			cursor: pointer; }
		&__name {
			font-size: 18px;
			color: $text-color;
			margin-bottom: 10px;
			text-transform: uppercase; }
		&__address {
			font-size: 16px;
			line-height: 1.5;
			font-weight: 300;
			color: $text-color; } }
	&__form {
		h3 {
			text-transform: uppercase;
			font-size: 18px;
			font-weight: 300;
			color: $text-color;
			margin-bottom: 20px; } } }
.form {
	textarea {
		display: block;
		width: 100% !important;
		color: $text-color;
		background-color: transparent;
		border: none;
		font-size: 18px;
		font-weight: 300;
		font-style: italic;
		border-bottom: 1px solid $text-color;
		padding: 0 20px;
		resize: vertical;
		@include placeholder-color($text-color); }
	&__input {
		width: 255px;
		margin-right: 70px;
		background-color: transparent;
		outline: none;
		border: none;
		border-bottom: 1px solid $text-color;
		font-size: 18px;
		font-weight: 300;
		font-style: italic;
		padding: 15px 20px;
		color: $text-color;
		margin-bottom: 65px;
		@include placeholder-color($text-color); }
	&__input-mod {
		margin-right: 0; }
	&__submit {
		text-align: center;
		input {
			padding: 20px;
			font-size: 18px;
			font-weight: 300;
			border: 1px solid $text-color;
			background-color: transparent; } } }
.social {
	position: absolute;
	top: 90%;
	right: 210px;
	&__list {
		padding: 0;
		margin: 0;
		width: 100%;
		display: flex;
		align-items: center;
		justify-content: flex-end; }
	&__link {
		padding: 20px 30px;
		border: 1px solid $text-color;
		margin: 15px; }
	&__link.facebook {
		background: url('/icons/facebook.svg') no-repeat center center; }
	&__link.insta {
		background: url('/icons/insta.svg') no-repeat center center; } }








