body {
	padding: 0;
	margin: 0;
	background: #FFF;
	line-height: 1.4;
	font-family: 'Merriweather', serif;
	position: relative;
	overflow-x: hidden; }
img {
	max-width: 100%;
	height: auto; }
.container {
	max-width: 1740px;
	padding: 0 90px;
	margin: 0 auto;
	height: 100%; }
.title {
	position: relative;
	display: block;
	// width: 100%
	color: #3c3c3c;
	font-size: 40px;
	font-weight: lighter;
	text-align: center;
	padding-bottom: 30px;
	&:after {
		content: '';
		position: absolute;
		width: 100%;
		height: 35px;
		top: 100%;
		left: 0;
		background: url('/icons/decoration.svg') no-repeat center center; } }
